<template>
  <div class="content">
    <div class="wraper">
			<div class="wrap">
				<div class="cont-info clear">
					<img class="cont-info-heart" src="../assets/blog_heart.png" />
					<div class="cont-info-boy fl">
						<a href="#">
							<img src="../assets/blog_boy.png" />
							<h5>胡小呆</h5>
						</a>
						<p>前行的道路，多少会有磕磕绊绊，坚持自己的内心，勇敢的踏出那一步，风雨过后，便是美丽的彩虹。</p>
					</div>
					<div class="cont-info-girl fr">
						<a href="#">
							<img src="../assets/blog_girl.png" />
							<h5>曹小萌</h5>
						</a>
						<p>你要相信：越努力，越幸运。你的负担将变成礼物，你受的苦将照亮你的路。</p>
					</div>
				</div>
				<div class="cont-article clear">
					<div class="cont-article-line">&nbsp;</div>
          <div class="cont-article-boy fl">
            <my-boybox></my-boybox>
          </div>
          <div class="cont-article-girl fr">
            <my-girlbox></my-girlbox>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BoySummary from './BoySummary'
import GirlSummary from './GirlSummary'
export default {
  name: "Content",
  data() {
    return {};
  },
  components: {
    'my-boybox': BoySummary,
    'my-girlbox': GirlSummary,
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .wraper{
    background: url('../assets/body_repeat.jpg') repeat;
    width: 100%;
    overflow: hidden;
    .wrap {
      margin: 0 auto;
      width: 1200px;
      margin-top: 20px;
      overflow: hidden;
    }
    .cont-info {
      position: relative;
      padding-top: 25px;
      width: 1200px;
      height: 340px;
      color: #323232;
      h5 {
        margin-bottom: 7px;
        padding-bottom: 7px;
        border-bottom: 3px solid #000000;
        font-size: 24px;
        text-align: center;
        &::before {
          content: "";
          position: absolute;
          bottom: -5px;
          left: 50%;
          margin-left: -2px;
          width: 4px;
          height: 80px;
          background: #d7e4ed;
        }
      }
      p {
        width: 515px;
        font-size: 16px;
        line-height: 30px;
        text-indent: 2em;
      }
    }
    .clear {
      clear: both;
    }
    .cont-info-heart {
      display: block;
      position: absolute;
      top: 170px;
      left: 50%;
      margin-left: -79px;
      width: 160px;
      height: 140px;
      z-index: 3;
    }
    img {
      border: 0;
      vertical-align: middle;
    }
    .cont-info-boy, 
    .cont-info-girl {
      width: 50%;
      p{
        margin-left: 15px;
      }
    }
    .cont-info-boy>a, 
    .cont-info-girl>a {
      color: #333;
    }
    .cont-info-boy img, 
    .cont-info-girl img {
      display: block;
      width: 180px;
      height: 180px;
      margin: 0 auto;
      border-radius: 100%;
    }
    .cont-article {
      position: relative;
      padding-bottom: 50px;
      overflow: hidden;
    }
    .cont-article-line {
      position: absolute;
      left: 50%;
      margin-left: -2px;
      width: 4px;
      height: 100%;
      background: #d7e4ed;
    }
    .cont-article-boy, 
    .cont-article-girl {
      width: 532px;
    }
  }
</style>



