<template>
  <div class="main">
      <my-header></my-header>
      <my-banner></my-banner>
      <my-content></my-content>
      <my-footer></my-footer>
  </div>
</template>
<script>
import Header from "./Header"
import Banner from "./Banner"
import Footer from "./Footer"
import Content from "./Content"
export default {
  name: 'Main',
  data() {
      return {}
  },
  components: {
    'my-header': Header,
    'my-banner': Banner,
    'my-footer': Footer,
    'my-content': Content,
  }
}
</script>




