  <div class="banner">
    <span class="banner-title">Record  Our  Love  Story  With  Words</span>
    <span class="banner-introduce">欢迎来到胡小呆&曹小萌的情侣博客，这里是我们在互联网的“窝”，这里有技术的学习笔记，也有好看的博客模板，更有我们的爱情故事，如果你喜欢我们的情侣博客，记得留言与我们互动哟！</span>
