<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .banner{
    padding-top: 90px;
    width: 100%;
    height: 590px;
    background: url('../assets/banner.jpg') no-repeat center center;
    .banner-title {
      display: block;
      margin-top: 70px;
      margin-bottom: 150px;
      font-family: arial;
      font-size: 60px;
      color: #ffffff;
      text-align: center;
    }
